@import "../../assets/styles/variables";
@import "../../assets/styles/mixins";

.brk-cart {
	box-shadow: 0 6px 30px 0 rgba(0, 0, 0, 0.1);
	position: relative;

	&:after {
		content: '';
		position: absolute;
		left: 0;
		bottom: 0;
		right: 0;
		height: 2px;
		background-image: linear-gradient(to right, var(--brk-base-3), var(--brk-secondary-3));
	}

	table {
		width: 100%;
		border-collapse: collapse;
		margin: 0;
		border: 0;
		box-shadow: none;

		thead {
			box-shadow: 0 5px 10px rgba(0,0,0,.06), inset 0 -3px #fafafa;
			position: relative;
			z-index: 9;

			@media (max-width: 991px) {
				display: none;
			}

			th {
				padding: 25px 15px 25px 40px;
				position: relative;
				font-weight: 700;
				font-size: rem(17);
				line-height: 18px;
				border: 0;
			}
		}

		tbody {

			tr {
				border-bottom: 1px solid #e9e9e9;

				@media (max-width: 991px) {
					&:not(:last-child) {
						border-bottom: 3px solid #e9e9e9;
					}
				}
			}

			td {
				padding: 33px 0;
				border: 0;
				vertical-align: middle;

				@media (max-width: 991px) {
					padding: 20px;
					border-bottom: 1px solid #e9e9e9;

					&:last-child {
						border-bottom: 0;
					}

					&:not(:first-child) {
						display: flex;
						justify-content: space-between;
						align-items: center;
					}
				}

				&:first-child {
					@media (min-width: 1230px) {
						width: 190px;
					}
				}

				&:nth-child(2) {
					@media (min-width: 992px) {
						padding: 33px 25px;
					}

					@media (min-width: 1230px) {
						width: 370px;
					}
				}

				&:nth-child(3),
				&:nth-child(4),
				&:nth-child(5) {
					@media (min-width: 992px) {
						padding: 33px 15px 33px 40px;
					}
				}

				&:nth-child(4) {
					@media (min-width: 1230px) {
						width: 200px;
					}
				}

				&:nth-child(5) {
					@media (min-width: 992px) {
						width: 160px;
					}

					@media (min-width: 1230px) {
						width: 220px;
					}
				}
				
				&:before {
					content: attr(aria-label);
					display: block;
					font-weight: 700;
					font-size: rem(17);
					line-height: 18px;

					@media (min-width: 992px) {
						display: none;
					}
				}
			}
		}

		td, th {
			text-align: left;
		}
	}

	@media (max-width: 991px) {
		table, thead, tbody, th, td, tr {
			display: block;
		}
	}

	&__remove {
		border: 0;
		position: relative;
		width: 32px;
		height: 32px;
		border-radius: 50%;
		background-color: #eeeeee;
		transition: background-color .3s;

		@media (min-width: 992px) {
			margin: 0 40px;
		}

		&:before,
		&:after {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			background-color: #2a2b2f;
			width: 2px;
			height: 12px;
			transition: background-color .3s;
		}

		&:before {
			transform: translate(-50%, -50%) rotate(45deg);
		}

		&:after {
			transform: translate(-50%, -50%) rotate(-45deg);
		}

		&:hover {
			background-color: #ff0000;

			&:before,
			&:after {
				background-color: #ffffff;
			}
		}
	}

	&__img {
		width: 68px;
		height: 68px;
	}

	&__count {
		display: flex;
		align-items: center;

		input {
			width: 74px;
			border: 0;
			text-align: center;
			font-size: rem(16);
			line-height: 24px;
			height: 24px;
			font-family: "Montserrat", sans-serif;
			font-weight: 700;
			-moz-appearance:textfield;

			&::-webkit-outer-spin-button,
			&::-webkit-inner-spin-button {
				-webkit-appearance: none;
			}
		}

		button {
			width: 16px;
			height: 24px;
			position: relative;

			i {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
			}
		}
	}

	&__footer {
		display: flex;
		flex-wrap: wrap;
		border-bottom: 1px solid #e9e9e9;

		&-coupon {
			flex: 0 0 525px;
			max-width: 525px;
			padding: 65px 55px 65px 40px;

			@media (max-width: 1229px) {
				flex: 0 0 450px;
				max-width: 450px;
				padding: 65px 35px;
			}

			@media (max-width: 991px) {
				flex: 0 0 100%;
				max-width: 100%;
				padding: 20px 35px;
			}

			@media (max-width: 575px) {
				padding: 20px 15px;
			}

			.btn-lg {
				padding: 15px 45px;
			}
		}

		&-total {
			flex-basis: 0;
			flex-grow: 1;
			max-width: 100%;
			background-color: #f8f9fb;
			border-left: 1px solid #e9e9e9;
			padding: 28px 15px 28px 73px;

			@media (max-width: 1229px) {
				padding: 28px 15px 28px 35px;
			}

			@media (max-width: 991px) {
				flex: 0 0 100%;
				max-width: 100%;
				border-left: none;
				border-top: 1px solid #e9e9e9;
			}

			@media (max-width: 575px) {
				padding: 20px 15px;
			}

			&-item {
				display: flex;
				align-items: center;
				padding: 11px 0;

				@media (max-width: 575px) {
					padding: 8px 0;
				}

				div:first-child {
					flex: 0 0 292px;
					max-width: 292px;

					@media (max-width: 1229px) {
						flex: 0 0 220px;
						max-width: 220px;
					}

					@media (max-width: 575px) {
						flex: 0 0 180px;
						max-width: 180px;
					}
				}
			}
		}
	}

	.btn {
		margin: 0 5px;
	}

	.btn-inside-out-invert {
		font-size: rem(13);

		> .after,
		> .before {
			background: #fff;
			color: #272727;
			padding-left: 30px;
		}
		&.btn-icon [class*="fa-"]::before {
			color: #242424;
		}

		&:hover.btn-icon [class*="fa-"]::before {
			color: #fff;
		}
	}
	.btn-inside-out.btn-icon-right .icon-inside ~ .text {
		padding-right: 0;
	}

	.btn-icon-right i {
		right: 59px;
	}

}